<template>
  <div>
    <div v-show="ojbkShow" class="zz"></div>
    <div v-show="ojbkShow" class="outer_box1">
      <div class="box">
        <span class="stop"></span>
        <el-input type="number" v-model="stopIn" placeholder=""></el-input>
      </div>
      <div class="box">
        <span class="down_green stop"></span>
        <el-input type="number" v-model="stopOut" placeholder=""></el-input>
      </div>
      <div class="box">
        <el-button @click="hide()" type="info"></el-button>
        <el-button @click="ojbk()" type="danger"></el-button>
      </div>
    </div>
    <div class="tab">
      <ul>
        <li @click="tabClick(index)" v-for="(item,index) in tab" :key="index" :class="{active:index == tabIndex}">{{item.title}}</li>
      </ul>
    </div>
    <div v-show="tabIndex == 1" class="tab1">
      <!--  -->
      <div class="search">
        <i><img src="../../../static/images/search.png" /></i>
        <input @blur.prevent="changeCount()" placeholder="" v-model="searchInput" type="" name="" id="" />
      </div>
      <div class="search_div">
        <ul>
          <li @click="postSearch(item.code)" v-for="(item,index) in addInput" :key="index">
            <span class="left">{{item.code | filtersName}}</span>
            <span style="margin-left:.2rem; float:right;" class="left">{{item.name}}</span>
            <!-- <span @click="addMyself(item.code)" class="right"></span> -->
          </li>
        </ul>
      </div>
      <div class="title">
        <p class="sx"></p>
        <p class="left"></p>
        <p @click="removeAll()" class="right"></p>
      </div>
      <div class="searh_history">
        <li v-for="(item,index) in history" :key="index" class="li">
          <router-link :to="{path : '/ks',query:{code:item[0]}}">
            <p class="name">{{item[1]}} {{item[0] | filtersName}}</p>
            <p :class="{down_green:(item[4]-item[3])/item[3] <0}" class="num">{{item[3]}}</p>
            <p :class="{down_green:(item[4]-item[3])/item[3] <0}" class="percentage">{{(item[4]-item[3])/item[3] | toPercentage}}</p>
          </router-link>
          <p @click="remove(item[0])" class="close iconfont">&#xe621;</p>
        </li>
        <div style="margin-top:.26rem;" class="title">
          <div style="width:3rem;margin:0 auto">
            <div @click="upDown(0)" class="mleft">&lt;</div>
            <div @click="upDown(1)" class="mright">&gt;</div>
          </div>
        </div>
        <div class="title">
          <p class="sx"></p>
          <p class="left"></p>
        </div>
      </div>
      <div class="hot">
        <ul>
          <router-link tag="li" :to="{path : '/ks',query:{code:share[0]}}" :class="{nomargin:index == 2}" v-for="(share,index) in hotShares" :key="index">
            <p class="top">{{share[1]}}</p>
            <p class="center">{{share[0] | filtersName}}</p>
            <p :class="{down_green:(share[4]-share[3])/share[3]<0}" class="num">{{parseFloat(share[4]).toFixed(2)}}</p>
            <p :class="{down_green:(share[4]-share[3])/share[3]<0}" class="bottom">
              {{(share[4]-share[3]).toFixed(2)}}&nbsp;&nbsp;&nbsp;{{(share[4]-share[3])/share[3] | toPercentage}}
            </p>
          </router-link>
        </ul>
      </div>
    </div>

    <div v-show="tabIndex == 0" class="tab2">
      <div class="money">
        <ul>
          <li class="left">
            <p class="top"></p>
            <p class="num">{{accountCount.asset}}</p>
          </li>
          <li class="right">
            <p class="top"></p>
            <p class="num">{{accountCount.can_use}}</p>
          </li>
        </ul>
        <ul>
          <li class="left">
            <p class="top"></p>
            <p class="num">{{accountCount.market_value}}</p>
          </li>
          <li class="right">
            <p class="top"></p>
            <p class="num">{{accountCount.float_earn}}</p>
          </li>
        </ul>
      </div>
      <div class="snav">
        <ul>
          <li @click="snavClick(index)" :class="{active:index == sindex}" class="txt" v-for="(item,index) in snav" :key="index">{{item.title}}</li>
          <li class="sx"></li>
          <li class="sx"></li>
        </ul>
      </div>
      <div class="snav1">
        <div v-show="sindex == 0" v-if="nowStatus != 0" v-for="(item,index) in nowCc" :key="'chi'+index" class="now">
          <div @click="isShow(index)" class="title_box">
            <div class="left">{{item.name}}</div>
            <div class="right">{{item.code}}</div>
          </div>
          <ul v-show="item.is_show == 1">
            <li> <span>{{item.xingyongjin}}</span></li>
            <li> <span>{{item.price}}</span></li>
            <li>  <span>{{item.max_price}}</span></li>
            <li> <span>{{item.num}}</span></li>
            <li>  <span :class="{down_green:item.current_price-item.price<0}" style="color:#ef584d">{{item.current_price}}</span></li>
            <li>  <span>{{item.min_price}}</span></li>
            <li> <span :class="{down_green:item.float_earn<0}" style="color:#ef584d">{{item.float_earn}}</span></li>
            <li style="width:60%"> <span>{{item.created_at}}</span></li>

            <div v-for="(item2,index) in item.xingyongjin_appends" :key="index" class="box">
                <li style="width:50%"><span>{{item2.money}}</span></li>
                <li style="width:50%"><span>{{item2.created_at}}</span></li>
            </div>

            <li>  <span>{{item.market_value}}</span></li>


            <div class="sell">
              <div class="box">
                <div class="left"><span v-if="item.is_sell == 1"></span><span v-if="item.is_sell == 2"></span></div>
              </div>
              <div class="box">
                <div class="left">:{{(item.current_price-item.min_price).toFixed(2)}}</div>
              </div>
              <div class="box">
                <div class="left">1400</div>
              </div>

            </div>
            

            <div class="sell2">
              <div @click="sell(item.tactic_id)" class="button active"></div>
              <div @click="stop(item.tactic_id,item.max_price,item.min_price,item.min_min_price)" class="button"></div>              
              <div v-if="item.is_sell == 1" @click="delay(item.tactic_id,2)" class="button"></div>              
              <div v-if="item.is_sell == 2" @click="delay(item.tactic_id,1)" class="button nomargin"></div>              
            </div>
            <div v-if="item.is_add == 1" class="sell2">               
              <input class="button" style="flex:.6;" v-model="xingyongjin" type="text"> 
              <div @click="addXyj(item.tactic_id,xingyongjin)" style="flex:.4;margin-left:.2rem;" class="button active"></div>
            </div>
          </ul>
        </div>

<div v-show="sindex == 1" @click="isShow3(index)" v-for="(item,index) in wt" :key="index" class="now">
          <div class="title_box">
            <div class="left">{{item.name}}&nbsp;&nbsp;&nbsp;<span style="font-size:.3rem;">{{item.code}}</span></div>
            <div v-if="item.out_in == 0" class="right"></div>
            <div v-if="item.out_in == 2" class="right"></div>
          </div>
          <ul v-show="item.is_show == 1">
            <li> <span>{{item.price}}</span></li>
            <li> <span>{{item.current_price}}</span></li>
            <li> <span>{{item.num}}</span></li>
            <li> <span>{{item.count_xingyongjin}}</span></li>
            <li> <span>{{item.max_price}}</span></li>
            <li> <span>{{item.min_price}}</span></li>
            <li style="width:100%;"> <span>{{item.created_at}}</span></li>        
          </ul>
        </div>

        <div v-show="sindex == 2" @click="isShow2(index)" v-for="(item,index) in historyInfo" :key="'his_'+index" class="now">
          <div class="title_box">
            <div class="left">{{item.name}}</div>
            <div class="right">{{item.code}}</div>
          </div>
          <ul v-show="item.is_show == 1">
            <li> <span>{{item.xingyongjin}}</span></li>
            <li> <span>{{item.price}}</span></li>
            <li> <span>{{item.deal_money}}</span></li>
            <li> <span>{{item.num}}</span></li>
            <li> <span>{{item.profit}}</span></li>
            <li> <span>{{item.rate}}</span></li>
            <li> <span>{{item.created_at}}</span></li>
            <li> <span>{{item.deal_time}}</span></li>
            <li> <span>{{item.cangfei}}</span></li>
            <li> <span>{{item.count_diyanfei}}</span></li>
            <li style="width:100%"> <span>{{item.pingcang_type}}</span></li>            
          </ul>
        </div>
        <p v-if="nowStatus == 0" class="nothing">~</p>
      </div>
    </div>
  </div>
</template>

<script>
import { Dialog } from "vant";

import Axios from "../../common/js/getAxios";
export default {
  filters: {
    //
    toPercentage(value) {
      let str = Number(value * 100).toFixed(2);
      str += "%";
      return str;
    }
  },
  data() {
    return {
      xingyongjin:1000,
      wt : [],
      time : "",
      //      
      input: '',
      //
      hotShares : [],
      //
      historyInfo : [],
      //
      accountCount : {},
      tmpId : "",
      ojbkShow : false,
      stopIn : 0,
      stopOut : 0,
      //
      tmpOut : 0,
      // 
      historyPage: 1,
      //
      history: [],
      //
      searchInput: [],
      //
      addInput: [],
      //
      nowCc: [],
      //
      nowStatus: 0,
      sindex: 0,
      tabIndex: 0,
      tab: [
        {
          title: ""
        },
        // {
        //   title: ""
        // }
        
      ],
      snav: [
        {
          title: ""
        },
        {
          title: ""
        },
        {
          title: ""
        }
      ]
    };
  },
  watch: {
    searchInput(v) {
      // this.searchCl(v);
      Axios.axiosGet("/api/stock/search", { code: v }).then(
        res => {
          //push
          this.addInput = res.data.data;          
        },
        err => {
          console.log(err);
        }
      );

    }
  },
  mounted() {
    this.getNow();
    this.showHistory();
    this.getAccountCount();
    this.getHot();
    // this.repeat();
    this.time = setInterval(()=>{
      // this.getNow();
      // this.getAccountCount();
    },3000)
  },
  //
  beforeDestroy(){
    clearInterval(this.time)
  },
  methods: {
    //
    getWt(){
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosGet(
        "/api/strategyEntrust",
        {
          // code: code
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {          
            console.log(res);          
          this.wt = res.data.data;    
        },
        err => {
          console.log(err);
        }
      );
    },
    // repeat(){
    //   this.time = setInterval(()=>{
    //     this.getNow();    
    //     this.getAccountCount();
    //   },1000)
    // },

    //K
    postSearch(code) {
      let userToken = localStorage.getItem("Authorization");
      Axios.postAxios(
        "/api/search",
        {
          code: code
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          if (res.status) {
            console.log("");
          }
          //K
          this.$router.push({ path: "/ks", query: { code: code } });
        },
        err => {
          console.log(err);
        }
      );
    },

    
    addXyj(id,xyj){
      let userToken = localStorage.getItem("Authorization");
      Axios.postAxios(
        '/api/tactics/addshoudong',
        {
          tactic_id : id,
          // xingyongjin : this.input
          xingyongjin : xyj
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(res=>{
          console.log(res.data)          
          if(res.data.status){            
            this.$message.success(res.data.msg);
          }else{            
            this.$message.error(res.data.msg);
          }
      },err=>{
        console.log(err);
        this.$message.error('');
      })
    },
    //
    getHot(){
      Axios.axiosGet(
        "/api/goods/hot",
        {
        },
        {
        }
      ).then(
        res => {
          // console.log(res)
          this.hotShares = res.data.data;
        },
        err => {}
      );
    },
    //
    getHistoryInfo(){
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosGet(
        "/api/strategyHistory/normal",
        {
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          this.historyInfo = res.data.data;
          console.log(res)
        },
        err => {}
      );
    },
    //
    getAccountCount(){
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosGet(
        "/api/accountCount/normal",
        {
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          this.accountCount = res.data.data;
        },
        err => {}
      );
    },
    // 
    async sell(id){
      let userToken = localStorage.getItem("Authorization");

      let ok = true;
      await Dialog.confirm({
        title: '',
        message: ''
      }).then(() => {
        // on confirm
      }).catch(() => {
        ok = false;
      });

      if(!ok){
        return false;
      }
      Axios.postAxios(
        '/api/tactics/sale',
        {
          tactic_id : id
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(res=>{
        if(res.data.status == false){          
           this.$message.error(res.data.msg);
        }else{
          this.$message.success(res.data.msg)
        this.getNow();
        }
      },err=>{
        console.log(err);
      })
    },
    stop(id,max,min,min_min_price){
      this.tmpId = id;
      this.ojbkShow = true;
      this.stopIn = max;
      this.stopOut = min;
      this.tmpOut = min_min_price;
    },
    hide(){
      this.tmpId = "";
      this.ojbkShow = false;
    },
    // 
    // 
    ojbk(){
      let userToken = localStorage.getItem("Authorization");
      if(this.stopOut < this.tmpOut){
        this.$message.error(""+this.tmpOut);        
        return false;
      }
      Axios.postAxios(
        '/api/tactics/editprice',
        {
          tactic_id : this.tmpId,          
          max_price : this.stopIn,
          min_price : this.stopOut
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(res=>{
        console.log(res);
        if(!res.data.status){
          this.$message.success(res.data.msg);          
          return false;          
        }else{
          this.$message.success(res.data.msg);          
          this.getNow();
          this.ojbkShow = false;
        }
      },err=>{
        console.log(err);
      })
    },
    //
    delay(id,v){
      let userToken = localStorage.getItem("Authorization");
      Axios.postAxios(
        '/api/tactics/issell',
        {
          tactic_id : id,
          is_sell : v
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(res=>{
        if(!res.data.status){
          this.$message.error("");          

          return false;
        }else{
          this.$message.success("");

          this.getNow();
        }        
      },err=>{
        console.log(err);
      })
    },
    //
    remove(code){
      var that = this;
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosDelete(
        '/api/search',
        {
          code : code
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(res=>{        
        that.showHistory();
      },err=>{
        that.showHistory();
      })
    },
    // 
    removeAll(){
      var that = this;
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosDelete(
        '/api/search',
        {
          type : 1
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(res=>{        
        that.showHistory();
      },err=>{
        that.showHistory();
      })
    },
    //
    upDown(page) {
      if (page == 0) {
        if (this.historyPage == 1) {
          return false;
        }
        this.historyPage--;
      } else if (page == 1) {
        this.historyPage++;
      }
      console.log(this.historyPage);
      this.showHistory();
    },
    // 
    showHistory() {
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosGet(
        "/api/search",
        {
          page: this.historyPage
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          if (!res.data.status) {
            //            
            this.history.splice(0, this.history.length);
            this.upDown(0);
            return false;
          }
          //
          this.history.splice(0, this.history.length);
          this.history = this.history.concat(this.history, res.data.data);
        },
        err => {}
      );
    },
    //
    changeCount() {},
    //
    isShow(index) {
      if (this.nowCc[index].is_show == 0) {
        this.nowCc[index].is_show = 1;
      } else {
        this.nowCc[index].is_show = 1;
      }
    },
    isShow2(index) {
      this.historyInfo[index].is_show = 1;
      
    },
    isShow3(index) {
      this.wt[index].is_show = 1;
      
    },
    //
    getNow() {
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosGet(
        "/api/strategyChiCang/normal",
        {},
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          console.log(res)
          this.nowCc = res.data.data;
          this.nowStatus = res.data.status;
        },
        err => {          
          this.$router.push("/me");
        }
      );
    },
    //
    snavClick(index) {
      this.sindex = index;
      if(index == 2){
        this.getHistoryInfo();
      }
      if(index == 0){
        this.getNow();
      }
      if(index == 1){
        this.getWt();
      }
    },
    // 
    tabClick(index) {
      this.tabIndex = index;
      if(index == 1 ){
        this.getNow();
      }
    }
  }
};
</script>

<style lang="stylus" scoped>
@import '~@/assets/css/cl/cl';
  .tab ul{
    width:.7rem
  }
  .tab2 .money{
    background:#fff;
    p{
      color:#585858 !important;
    }
  }
  .tab2 .money ul li .num{
    margin-top:0 !important;
  }
  .tab2 .money ul{
    padding-bottom:.1rem;
    
  }
  .now ul li{
  font-size:.32rem
  // text-align:center
}
.now ul{
  padding: 0.2rem 0.25rem;
}
</style>





