<template>
  <div>
    <return-top>
      <p slot="title"></p>
    </return-top>
    <div class="tab1">
      <div class="search">
        <i><img src="../../../static/images/search.png" /></i>
        <input @blur.prevent="changeCount()" placeholder="" v-model="searchInput" type="" name="" id="" />
      </div>
      <div class="search_div">
        <ul>
          <li @click="postSearch(item.code)" v-for="(item,index) in addInput" :key="index">
            <span class="left">{{item.code}}</span>
            <span style="margin-left:.2rem;float:right;" class="left">{{item.name}}</span>
            <!-- <span @click="addMyself(item.code)" class="right"></span> -->
          </li>
        </ul>
      </div>
    </div>
    <div v-show="ojbkShow" class="zz"></div>
    <div v-show="ojbkShow" class="outer_box1">
      <div class="box">
        <span class="stop"></span>
        <el-input type="number" v-model="stopIn" placeholder=""></el-input>
      </div>
      <div class="box">
        <span class="down_green stop"></span>
        <el-input type="number" v-model="stopOut" placeholder=""></el-input>
      </div>
      <div class="box">
        <el-button @click="hide()" type="info"></el-button>
        <el-button @click="ojbk()" type="danger"></el-button>
      </div>
    </div>

    <!-- // -->
    <!-- <div class="createBuy">
      <br>
    </div> -->

    <div class="tab2">
      <div class="money">
        <ul>
          <li class="left">
            <p class="top"></p>
            <p class="num">{{accountCount.asset}}</p>
          </li>
          <li class="right">
            <p class="top"></p>
            <p class="num">{{accountCount.can_use}}</p>
          </li>
        </ul>
        <ul>
          <li class="left">
            <p class="top"></p>
            <p class="num">{{accountCount.market_value}}</p>
          </li>
          <li class="right">
            <p class="top"></p>
            <p class="num">{{accountCount.float_earn}}</p>
          </li>
        </ul>
      </div>
      <div class="snav">
        <ul>
          <li @click="snavClick(index)" :class="{active:index == sindex}" class="txt" v-for="(item,index) in snav" :key="index">{{item.title}}</li>
          <li class="sx"></li>
          <li class="sx"></li>
        </ul>
      </div>
      <div class="snav1">
        <div v-show="sindex == 0" v-if="nowStatus != 0" v-for="(item,index) in nowCc" :key="index" class="now">
          <div @click="isShow(index)" class="title_box">
            <div class="left">{{item.name}}</div>
            <div class="right">{{item.code}}</div>
          </div>
          <ul v-show="item.is_show == 1">
            <li> <span>{{item.xingyongjin}}</span></li>
            <li> <span>{{item.price}}</span></li>
            <li>  <span>{{item.max_price}}</span></li>
            <li> <span>{{item.num}}</span></li>
            <li>  <span>{{item.current_price}}</span></li>
            <li>  <span>{{item.min_price}}</span></li>
            <li> <span>{{item.float_earn}}</span></li>
            <li> <span>{{item.created_at}}</span></li>
            <li>  <span>{{item.market_value}}</span></li>
            <div class="sell">
              <div class="box">
                <div class="left"><span v-if="item.is_sell == 1"></span><span v-if="item.is_sell == 2"></span></div>
              </div>
              <div class="box">
                <div class="left">:{{(item.price-item.min_price).toFixed(2)}}</div>
              </div>
              <div class="box">
                <div class="left">1450</div>
              </div>
            </div>
            <div class="sell2">
              <div @click="sell(item.tactic_id)" class="button active"></div>
              <div @click="stop(item.tactic_id,item.max_price,item.min_price)" class="button"></div>
              <div @click="delay(item.tactic_id,2)" class="button"></div>
              <div @click="delay(item.tactic_id,1)" class="button nomargin"></div>
            </div>
            <div v-if="item.is_add == 1" class="sell2">
              <div style="flex:.6;" class="button">{{item.xingyongjin}}</div>
              <div @click="addXyj(item.tactic_id,item.xingyongjin)" style="flex:.4;margin-left:.2rem;" class="button active"></div>
            </div>
          </ul>
        </div>

        <div v-show="sindex == 2" @click="isShow2(index)" v-for="(item,index) in historyInfo" :key="item.code" class="now">
          <div class="title_box">
            <div class="left">{{item.name}}</div>
            <div class="right">{{item.code}}</div>
          </div>
          <ul v-show="item.is_show == 1">
            <li> <span>{{item.xingyongjin}}</span></li>
            <li> <span>{{item.price}}</span></li>
            <li> <span>{{item.deal_money}}</span></li>
            <li> <span>{{item.num}}</span></li>
            <li> <span>{{(Number(item.deal_money * 100).toFixed(2) - Number(item.price * 100).toFixed(2)) * item.num / 100}}</span></li>
            <li> <span>{{item.rate}}</span></li>
            <li> <span>{{item.created_at}}</span></li>
            <li> <span>{{item.deal_time}}</span></li>
            <li> <span>{{item.cangfei}}</span></li>
            <li> <span>{{item.count_diyanfei}}</span></li>
            <li style="width:100%"> <span>{{item.pingcang_type}}</span></li>         
          </ul>
        </div>
        <p v-if="nowStatus == 0" class="nothing">~</p>
      </div>
    </div>
  </div>
</template>

<script>
import Axios from "../../common/js/getAxios";
import ReturnTop from "@/components/ReturnTop";

export default {
  components: {
    ReturnTop
  },
  filters: {
    //
    toPercentage(value) {
      let str = Number(value * 100).toFixed(2);
      str += "%";
      return str;
    }
  },
  data() {
    return {
      input: "",
      accountCount: {},
      //
      historyInfo: [],
      //
      userInfo: {},
      tmpId: "",
      ojbkShow: false,
      stopIn: 0,
      stopOut: 0,
      //
      tmpOut: 0,
      // 
      historyPage: 1,
      //
      history: [],
      //
      searchInput: [],
      //
      addInput: [],
      //
      nowCc: [],
      //
      nowStatus: 0,
      sindex: 0,
      tabIndex: 0,
      tab: [
        {
          title: ""
        },
        {
          title: ""
        }
      ],
      snav: [
        {
          title: ""
        },
        {
          title: ""
        },
        {
          title: ""
        }
      ]
    };
  },
  watch: {
    searchInput(v) {
      // this.searchCl(v);
      Axios.axiosGet("/api/stock/search", { code: v }).then(
        res => {
          //push
          this.addInput = res.data.data;
        },
        err => {
          console.log(err);
        }
      );
    }
  },
  mounted() {
    this.getNow();
    this.getAccountCount();
  },
  methods: {
    //K
    postSearch(code) {
      let userToken = localStorage.getItem("Authorization");
      Axios.postAxios(
        "/api/search",
        {
          code: code
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          if (res.status) {
            console.log("");
          }
          //K
          this.$router.push({ path: "/ks", query: { code: code, isSim: 1 } });
        },
        err => {
          console.log(err);
        }
      );
    },
    addXyj(id, xyj) {
      let userToken = localStorage.getItem("Authorization");
      Axios.postAxios(
        "/api/tactics/addshoudong",
        {
          tactic_id: id,
          // xingyongjin : this.input
          xingyongjin: xyj
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          console.log(res);
          this.$message("");
        },
        err => {
          console.log(err);
          this.$message("");
        }
      );
    },

    //
    getHistoryInfo() {
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosGet(
        "/api/strategyHistory/analog",
        {},
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          this.historyInfo = res.data.data;
          console.log(res.data.data);
        },
        err => {}
      );
    },
    //
    getAccountCount() {
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosGet(
        "/api/accountCount/analog",
        {},
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          this.accountCount = res.data.data;
        },
        err => {}
      );
    },
    // 
    sell(id) {
      let userToken = localStorage.getItem("Authorization");
      Axios.postAxios(
        "/api/tactics/sale",
        {
          tactic_id: id
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          console.log(res);
          this.$message.success(res.data.msg);
          this.getNow();
        },
        err => {
          console.log(err);
          this.$message.success(err.data.msg);
        }
      );
    },
    stop(id, max, min) {
      this.tmpId = id;
      this.ojbkShow = true;
      this.stopIn = max;
      this.stopOut = min;
      this.tmpOut = this.stopOut;
    },
    hide() {
      this.tmpId = "";
      this.ojbkShow = false;
    },
    // 
    ojbk() {
      let userToken = localStorage.getItem("Authorization");
      if (this.stopOut < this.tmpOut) {
        this.$message("" + this.tmpOut);
        return false;
      }
      Axios.postAxios(
        "/api/tactics/editprice",
        {
          tactic_id: this.tmpId,
          max_price: this.stopIn,
          min_price: this.stopOut
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          console.log(res);
          if (!res.data.status) {
            this.$message("");
            return false;
          } else {
            this.$message("");
            this.getNow();
            this.ojbkShow = false;
          }
        },
        err => {
          console.log(err);
        }
      );
    },
    //
    delay(id, v) {
      let userToken = localStorage.getItem("Authorization");
      Axios.postAxios(
        "/api/tactics/issell",
        {
          tactic_id: id,
          is_sell: v
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          if (!res.data.status) {
            this.$message("");
            return false;
          } else {
            this.$message("");
            this.getNow();
          }
        },
        err => {
          console.log(err);
        }
      );
    },
    //
    remove(code) {
      var that = this;
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosDelete(
        "/api/search",
        {
          code: code
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          that.showHistory();
        },
        err => {
          that.showHistory();
        }
      );
    },
    // 
    removeAll() {
      var that = this;
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosDelete(
        "/api/search",
        {
          type: 1
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          that.showHistory();
        },
        err => {
          that.showHistory();
        }
      );
    },
    //
    upDown(page) {
      if (page == 0) {
        if (this.historyPage == 1) {
          return false;
        }
        this.historyPage--;
      } else if (page == 1) {
        this.historyPage++;
      }
      console.log(this.historyPage);
      this.showHistory();
    },
    //
    changeCount() {},
    //
    isShow(index) {
      if (this.nowCc[index].is_show == 0) {
        this.nowCc[index].is_show = 1;
      } else {
        this.nowCc[index].is_show = 0;
      }
    },
    isShow2(index) {
      console.log(index);
      if (this.historyInfo[index].is_show == 0) {
        this.historyInfo[index].is_show = 1;
      } else {
        this.historyInfo[index].is_show = 0;
      }
    },
    //
    getNow() {
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosGet(
        "/api/strategyChiCang/analog",
        {},
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          console.log(res);
          this.nowCc = res.data.data;
          this.nowStatus = res.data.status;
        },
        err => {
          //  this.$message("");
          this.$router.push("/me");
        }
      );
    },
    //
    snavClick(index) {
      console.log(index);
      this.sindex = index;
      if (index == 2) {
        this.getHistoryInfo();
      } else if (index == 0) {
        this.getNow();
      }
    },
    // 
    tabClick(index) {
      this.tabIndex = index;
      if (index == 1) {
        this.getNow();
      }
    }
  }
};
</script>

<style lang="stylus" scoped>
@import '~@/assets/css/cl/cl';
.createBuy {
  width: 50px;
  height: 50px;
  border-radius: 100%;
  background: #60bbfc;
  color: #fff;
  font-size: 14px;
  padding: 5px;
  text-align: center;
  position: absolute;
  right: 20px;
  bottom: 15px;
}
</style>





