<template>
  <div>
    <return-top>
      <p slot="title"></p>
    </return-top>

    <div class="order">
      <span class="qes"></span>
      <input v-model="code" type="text" placeholder="" class="a">
    </div>
    <div class="order">
      <span class="qes"></span>
      <input type="text" v-model="tel" placeholder="" class="a">
      <span @click="sendMsg()" class="send">{{getMsg}}</span>
    </div>

    <div class="order">
      <span class="qes"></span>
      <input type="text" v-model="msg" placeholder="" class="a">
    </div>

    <div class="order">
      <span class="qes"></span>
      <input type="password" v-model="password" placeholder="6~12" class="a">
    </div>
    <div class="order">
      <span class="qes"></span>
      <input type="password" v-model="passwordRepeat" placeholder="" class="a">
    </div>

    <div @click="sendRegist()" class="submit">
      
    </div>

    <!-- <div class="agree">
        <i class="iconfont">&#xe632;&nbsp;&nbsp;</i>
        <span class="txt"><span></span></span>
    </div> -->

  </div>
</template>

<script>
import ReturnTop from "@/components/ReturnTop";
import Axios from "../../common/js/getAxios";

export default {
  mounted() {
    this.code = this.$route.query.code;
  },
  data() {
    return {
      //
      num: 60,
      send: true,
      //
      getMsg: "",
      //
      code: "",
      //
      tel: "",
      // 
      verificationKey: "",
      // 
      msg: "",
      // 
      password: "",
      // 
      passwordRepeat: ""
    };
  },
  methods: {
    //
    sendMsg() {
      // console.log(this.send)
      if (!this.send) {
        this.$message(`${this.num}`);
        return false;
      }
      Axios.postAxios("/api/verificationCode/register", {
        phone: this.tel
      }).then(
        res => {
          console.log(res);
          if (res.data.msg == "") {
            this.$message("");
          } else if (res.data.status) {
            this.$message("");
            this.verificationKey = res.data.data.verificationKey;
            this.send = false;
            //this
            const _this = this;
            let time = setInterval(function() {
              _this.num--;
              _this.getMsg = _this.num;
              // console.log(_this.num)
              if (_this.num == 0) {
                _this.num = 60;
                clearInterval(time);
                _this.send = true;
                _this.getMsg = "";
              }
            }, 1000);
          } else if (res.data.msg == "") {
            this.$message("");
          } else if (res.data.msg == "") {
            this.$message("");
          } else if (res.data.msg == "") {
            this.$message("");
          }
        },
        err => {
          console.log(err.response);
        }
      );
    },
    //
    sendRegist() {
      //
      if (this.code == "" || this.code == undefined) {
        this.$message("");
        return false;
      } else if (this.tel == "") {
        this.$message("");
        return false;
      } else if (this.msg == "") {
        this.$message("");
        return false;
      } else if (this.password == "") {
        this.$message("");
        return false;
      } else if (this.passwordRepeat == "") {
        this.$message("");
        return false;
      }
      Axios.postAxios("/api/users", {
        code: this.code,
        phone: this.tel,
        verificationKey: this.verificationKey,
        verificationCode: this.msg,
        password: this.password,
        passwordRepeat: this.passwordRepeat
      }).then(
        res => {
          console.log(res);
          if (!res.data.status) {
            this.$message(res.data.msg);
          } else {
            this.$message(",");
            this.$router.push("/loginRegistrat");
          }
        },
        err => {
          console.log(err.response);
        }
      );
    }
  },
  components: {
    ReturnTop
  }
};
</script>
<style lang="stylus" scoped>
.agree {
  line-height: 0.4rem;
  width: 7.1rem;
  margin: 0.4rem auto 0;
  .iconfont {
    color: $red;
    font-size: 0.26rem;
  }
  .txt {
    font-size: 0.26rem;
    color: #707070;
    span {
      color: #0084ff;
    }
  }
}
@import '~@/assets/css/me/third';
.model {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.3);
  .box {
    border-radius: 6px;
    width: 6rem;
    position: absolute;
    height: auto;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    right: 0;
    margin: auto;
    .top {
      height: 0.8rem;
      border-radius: 6px 6px 0 0;
      background: #fc5155;
      color: #fff;
      line-height: 0.8rem;
      text-indent: 2em;
      width: 6rem;
    }
    .bottom {
      width: 6rem;
      background: #FFF;
      padding-bottom: 0.2rem;
      border-radius: 0 0 6px 6px;
      @extend .nobefore;
      p {
        width: 5.4rem;
        height: 0.82rem;
        margin: 0.2rem auto 0;
        border: 1px solid #eaeaea;
        line-height: 0.82rem;
        text-indent: 2em;
        font-size: 0.26rem;
        color: #303030;
      }
    }
  }
}
.send {
  text-align: center;
  color: #fc5155;
  font-size: 0.24rem;
  padding: 0.08rem;
  position: absolute;
  right: 0.26rem;
  line-height: 0.54rem;
  top: 0.15rem;
  border: 1px solid #fc5155;
  border-radius: 4px;
  min-width: 1.02rem;
}
.el-switch {
  position: absolute;
  right: 0.28rem;
  top: 0;
  bottom: 0;
  margin: auto;
}
.a {
  // text-align: right;
  line-height: 1rem !important;
  height: 1rem !important;
  img {
    width: 100%;
  }
}
.ts {
  position: absolute;
  left: 6.48rem !important;
  top: 0;
  bottom: 0;
  width: 0.6rem;
  height: 0.6rem;
  display: inline-block;
}
.order {
  line-height: 1rem;
}
</style>



