<template>
  <div>
    <div class="tab">
      <ul>
        <li @click="tabClick(index)" v-for="(item,index) in tab" :key="index" :class="{active:index == tabIndex}">{{item.title}}</li>
      </ul>
    </div>
    <div v-show="tabIndex == 0" class="tab1">
      <div class="search_box">
        <div style="margin:.4rem auto;float:none;" class="search">
          <i class="search_icon"><img src="../../../static/images/search.png" /></i>
          <input placeholder="//" type="" name="" id="" v-model="msearchInput" />
        </div>
        <div class="search_div">
          <ul>
            <li @click="postSearch(item.code)" v-for="(item,index) in searchArr" :key="index">
              <span class="left">{{item.code | filtersName}}</span>
              <span style="margin-left:.2rem; float:right;" class="left">{{item.name}}</span>
              <!-- <span @click="searchMyself(item.code)" class="right"></span> -->
            </li>
          </ul>
        </div>
        <!-- <i class="iconfont edit">&#xe938;</i> -->
      </div>
      <div class="title">
        <p class="name"></p>
        <p class="money"></p>
        <p class="num"></p>
      </div>
      <ul v-if="!isSelf" class="container">
        <router-link  tag="li" :to="{path : '/ks',query : {code:item[0]}}" 
        v-for="(item,index) in myShares" :key="index">
          <div @touchstart="touchStart($event,index)" @touchend="touchEnd" @touchmove="touchMove" class="move">
            <p class="name">{{item[1]}}<span>{{item[0] | filtersName}}</span></p>
            <p class="money">{{item[4]}}</p>
            <p :class="{down_green:(item[4]-item[3])/item[3]<0}" class="num">{{(item[4]-item[3])/item[3] | toPercentage}}</p>
            <!-- code -->
            <router-link tag="div" :to="{path:'/createSelf/',query : {code:item[0]}}" class="create"></router-link>
            <div @click.stop="deleteMyself(item[0])" ref="q" class="remove"></div>
          </div>
        </router-link>
      </ul>
    </div>
    <div v-show="tabIndex == 1" class="tab2">
      <div class="search">
        <i><img src="../../../static/images/search.png" /></i>
        <input @blur.prevent="changeCount()" placeholder="" v-model="searchInput" type="" name="" id="" />
      </div>
      <div class="search_div">
        <ul>
          <li @click="postSearch(item.code)" v-for="(item,index) in addInput" :key="index">
            <span class="left">{{item.code | filtersName}}</span>
            <span style="margin-left:.2rem;" class="left">{{item.name}}</span>
            <span @click="addMyself(item.code)" class="right"></span>
          </li>
        </ul>
      </div>

      <div class="hot">
        <ul>
          <li v-for="(item,index) in hot" :key="index">
            <p class="top">{{item.name}}</p>
            <p class="center">{{item.code}}</p>
            <p class="num" :class="{'green':item.up_value<0}">{{item.current_price}}</p>
            <p class="bottom" :class="{'green':item.up_value<0}">
              {{item.up_value}}&nbsp;&nbsp;&nbsp;{{item.up_ratio}}
            </p>
          </li>      
        </ul>
      </div>

      <div class="rank">
        <div class="title">
          <span></span>
          <router-link tag="span" to="selfMore" class="iconfont">&#xe620;</router-link>
        </div>

        <div class="wrapper" ref="wrapper">

          <div class="scroll_container">

            <div class="container">
              <ul>
                <router-link tag="li" :to="{path:'/ks',query:{code : item[0]}}" v-for="(item,index) in rank" :key="index">
                  <div class="name">
                    <p class="top">{{item[1]}}</p>
                    <p class="bottom">{{item[0] | filtersName}}</p>
                  </div>
                  <div class="num">{{item[4]}}</div>
                  <div :class="{down_green:(item[4]-item[3])/item[3]<0}" class="percentage">{{(item[4]-item[3])/item[3] | toPercentage}}</div>
                </router-link>
              </ul>
            </div>
          </div>

        </div>

      </div>

    </div>
  </div>
</template>

<script>
import Axios from "../../common/js/getAxios";
//better-scroll
import BScroll from "better-scroll";
export default {
  filters: {
    //
    toPercentage(value) {
      let str = Number(value * 100).toFixed(2);
      str += "%";
      return str;
    }
  },
  mounted() {
        this.getRank();
    this.showMyself();
    this.getHot();
    //scroll
    this.scroll = new BScroll(this.$refs.wrapper, {
      click: "true",
      pullUpLoad: {
        threshold: 10
      }
    });
    this.lazyLoad();
  },
  data() {
    return {
      hot:"",
      //X
      initLeft:"",
      moveIndex: 0,
      //X
      moveLeft:"",
      // right
      moveRight : "-2rem",
      //
      isSelf : true,      
      //
      msearchInput: "",
      //
      searchArr : [],
      //
      myShares: [],
      //2page
      page: 1,
      tabIndex: 1,
      //
      addInput: [],
      //
      searchInput: "",
      //
      rank: [],
      tab: [
        {
          title: ""
        },
        {
          title: ""
        }
      ]
    };
  },
  watch: {
    searchInput(data) {
      //,
      this.addInput.splice(0, this.addInput.length);
      this.getAddSearch(data);
    },
    msearchInput(data) {
      this.searchArr.splice(0,this.searchArr.length);
      this.searchMyself(data);
    }
  },
  methods: {
    //
    getHot() {
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosGet(
        "/api/hotshare",
        {},
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          console.log(res);
          this.hot = res.data;
        },
        err => {
          //  this.$message("");
          this.$router.push('/me');
        }
      );
    },
    //
    deleteMyself(code){
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosDelete('/api/selfstock',{
        code : code
      },{
        Authorization: "Bearer " + userToken
      }).then(res=>{
        this.$message("");        
        this.showMyself();
      },err=>{
        console.log(err);
        this.$message("");        
      })
    },
    //
    touchMove(v){
      this.moveLeft = v.targetTouches[0].clientX;  
      this.moveRight = (this.moveLeft-this.initLeft)*0.019;
      // console.log(this.moveLeft-this.initLeft)      
      if(this.moveRight<0){
        this.moveRight = 0;
      }else{
        this.moveRight = 2;
      }
      // console.log(this.moveRight)
      this.moveRight = -this.moveRight+'rem';
      this.$refs.q[this.moveIndex].style.right = this.moveRight;
    },
    touchStart(v,index){
      this.initLeft = v.targetTouches[0].clientX;
      this.moveIndex = index;
      // console.log(this.$refs[index])
    },
    touchEnd(v){
      // console.log(v)
    },
    //
    //K
    postSearch(code) {
      let userToken = localStorage.getItem("Authorization");
      Axios.postAxios(
        "/api/search",
        {
          code: code
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          if (res.status) {
            console.log("");
          }
          //K
          this.$router.push({ path: "/ks", query: { code: code } });
        },
        err => {
          console.log(err);
        }
      );
    },
    //
    showMyself() {
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosGet(
        "/api/selfstock",
        {},
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          if(res.data.status){
            this.myShares = this.myShares.concat(this.myShares, res.data.data);
            this.isSelf = false;
          }else{
            this.isSelf = true;
          }
          console.log(res)
        },
        err => {
          //  this.$message("");
          this.$router.push('/me');
        }
      );
    },
    //
    searchMyself(value) {
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosGet(
        "/api/selfstock/search",
        {
          code: value
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(res => {
        console.log(res);
        if(!res.data.status){
          return false;
        }
        this.searchArr = res.data.data;
      }, err => {
        console.log(err);
      });
    },
    //
    addMyself(value) {
      let userToken = localStorage.getItem("Authorization");
      Axios.postAxios(
        "/api/selfstock",
        {
          code: value
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          if(!res.data.status){
             this.$message("");
            return false;
          }
           this.$message("");
        },
        err => {
           this.$message("");
        }
      );
    },
    //2
    changeCount() {
      // this.addInput.splice(0, this.addInput.length);
    },
    //
    lazyLoad() {
      //dom
      this.$nextTick(() => {
        // 
        this.scroll.on("pullingUp", () => {
          console.log("run");
          this.getRank();
        });
      });
    },
    //
    getAddSearch(data) {
      Axios.axiosGet("/api/stock/search", { code: data }).then(
        res => {
          //push
          let input = res.data.data;
          if (input) {
            input.forEach(element => {
              this.addInput.push(element);
            });
          }
        },
        err => {
          console.log(err);
        }
      );
    },
    //
    getRank() {
      Axios.axiosGet("/api/stock", { page: this.page }).then(
        res => {
          console.log(res);
          this.rank = this.rank.concat(res.data.data);
          //betterscroll
          this.scroll.finishPullUp();
          //scroll
          this.scroll.refresh();
          this.lazyLoad();
          this.page++;
        },
        err => {
          console.log("err:" + err);
        }
      );
    },
    tabClick(index) {
      this.tabIndex = index;
      //
      if (index == 1) {
        this.getRank();
      } else if (index == 0) {
        //
        this.myShares.splice(0, this.myShares.length);
        console.log(this.myShares);
        // 
        this.showMyself();
      }
    }
  }
};
</script>

<style lang="stylus" scoped>
@import '~@/assets/css/self/self';
  .hot li:nth-child(3){
    margin-right:0 !important;
  }
</style>


  


