
<template>
    <div>
        <return-top>
          <p slot="title"></p>
        </return-top>
        <div class="order">
            <span class="qes"> </span>
            <div @click="showSelect" class="a">
              <el-input placeholder="" v-model="cityEx" >
         </el-input>
            </div>
        </div>

        <div class="order">
            <span class="qes"></span>
            <input type="text" class="a"> 
        </div>

        <div class="order">
            <span class="qes"></span>
            <input type="text" class="a"> 
        </div>

        <div class="order">
            <span class="qes"></span>
            <input type="text" class="a"> 
        </div>

        <div class="order">
            <span class="qes"></span>
            <input type="text" class="a"> 
        </div>

        <div @click="submitNext()" class="submit">
            
        </div>
    </div>
</template>

<script>
import ReturnTop from "@/components/ReturnTop";
import '@/../static/iosselect.css'
import '@/../static/iosSelect.js'
import '@/../static/zepto.js'
import {iosProvinces, iosCitys, iosCountys} from '@/../static/areaData_v2.js'
export default {
  data() {
    return {
      //
      cityEx:' ', 
      payIndex: 0,
      //
      payChoice: [
        {
          title: "",
          img: "../../../static/images/me/pay_img.jpg",
          choiceOk: "&#xe62b;"
        },
        {
          title: "",
          img: "../../../static/images/me/pay_img.jpg",
          choiceOk: "&#xe60f;"
        }
      ]
    };
  },
  components: {
    ReturnTop
  },
  methods: {
    //
  showSelect:function(){
  //id
   var levels = this.textToLevel(this.cityEx)
   var iosSelect = new IosSelect(3,
     [iosProvinces, iosCitys, iosCountys],
     {
       title: '',
       itemHeight: 35,
       relation: [1, 1],
    //
       oneLevelId: levels[0],
       twoLevelId: levels[1],
       threeLevelId: levels[2],
    //
       callback: (selectOneObj, selectTwoObj, selectThreeObj)=> {
        this.cityEx = selectOneObj.value + ' ' + selectTwoObj.value
        console.log(this.cityEx)
       }
   });
  },
//leveliosSelect
  textToLevel:function(text){
   var arr = []
   if (text) {
    var objs = text.split(' ')

    if (objs[0]) {
     iosProvinces.forEach((item, index)=>{
      if (item.value == objs[0]) {
       arr.push(item.id)
      }
     })
    }

    if (objs[1]) {
     iosCitys.forEach((item, index)=>{
      if (item.value == objs[1]) {
       arr.push(item.id)
      }
     })
    }

    if (objs[2]) {
     iosCountys.forEach((item, index)=>{
      if (item.value == objs[2]) {
       arr.push(item.id)
      }
     })
    }
   }

   return arr

  },
    select(index) {
      //
      this.payChoice.forEach(item => {
        item.choiceOk = "&#xe60f;";
      });
      this.payChoice[index].choiceOk = "&#xe62b;";
      this.payIndex = index;
    },
    submitNext() {
      this.$router.push({
        path: "third"
      });
    }
  }
};
</script>
<style lang="stylus" scoped>
@import '~@/assets/css/me/third';

</style>

<style lang="stylus">
#threeLevelContain{
  display none
}
#oneLevelContain,#twoLevelContain{
  width 50%
}
.el-input{
  width 5.2rem
  position absolute
  top 0
}
.ios-select-widget-box header.iosselect-header a{
  color #0084ff
}
.el-input__inner{
  font-size: .28rem
  color: #333333
  border-bottom: 1px solid #e5e5e5
  line-height: .8rem
  border none
  background none
}
</style>





